@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }

.flex {
  @include flex-container(center, row); }

.flexStart {
  display: flex;
  align-items: start;
  justify-content: center;
  flex-direction: column;
  padding: 0; }

.divPadding {
  padding: 6px; }


.main {
  display: flex;
  margin: 45px 15px;
  flex-direction: column;
  align-items: center; }

.expect {
  margin: 35px 0 0 0; }

.top {
  margin: 35px 0 0 0; }

.Progress {
  margin: 0 0 15px 0;
  width: 180px; }

.buttonAddGroup {
  padding: 0;
  margin: 10px 0 10px 0; }

.buttonStory {
  padding: 0;
  margin: 10px 0 10px 0; }

.div {
  margin: 0;
  padding: 0; }

.Progress__in {
  background: red; }

.Button--lvl-commerce {
  background-color: black; }


.Avatar__img {
  border: 3px;
  border-style: solid;
  border-color: white; }

.TabsDefaultUser {
  background-color: inherit;
  opacity: 1; }

.myItem {
  word-wrap: break-word;
  white-space: pre-wrap;
  width: 100%;
  user-select: none; }

.inventory {
  user-select: none; }

.card {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
  transition: 0.3s;
  margin: 10px 5px 10px 5px;
  background-color: white;
  border-radius: 10px;
  border: 1px;
  border-style: solid;
  border-color: white;
  padding: 5px; }


.bg-color {
  background-color: white; }

.Cell__indicator+.Cell__aside:not(:empty) {
  color: black; }
